.header {
  box-sizing: border-box;
  border-bottom: 1px solid rgba(84,84,84,.7);
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: flex-end;
}

@media screen and (max-width: 767px) {
  .header {
    padding-left: 0 30px;
    margin-bottom: 40px;
  }
}

