.popup__container {
  width: 430px;
  background: #fff;
  box-shadow: 0px 0px 25px rgba(0, 0, 0, .15);
  border-radius: 10px;
  padding: 0 36px 36px;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  box-sizing: border-box;
  position: relative;
  }

@media screen and (max-width: 767px) {
  .popup__container {
    width: 282px;
    padding: 0 22px 25px;
  }
}



